body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.button {
  display: inline-block;
  margin: .3em;
}

.foodlist {
  border: 10px solid #B3322B;
  border-radius: 10px;
  padding: .3em;
  background-color: transparent;
  opacity: .9;
  max-width: 90%;
  height: auto;
  -ms-flex-line-pack: center;
      align-content: center;
  border: 5px solid black;
  display: block;
  margin: auto;
}

.formContainer {
  width: 500px;
  clear: both;
  margin-right: 10em;
}

.formContainer input {
  width: 100%;
  clear: both;
}



